.App {
  font-family: sans-serif;
  text-align: center;
}
* {
  box-sizing: border-box;
}
.IconStyles {
  font-size: xxx-large;
  color: #34e8eb;
}

.CartStyles {
  position: relative;
  display: inline-block;
}
.CountStyles {
  position: absolute;
  top: 0;
  right: 0;
  color: #fc110d;
  font-weight: bold;
}
.AddButton {
  width: 100px;
  height: 30px;
  margin: 5px 5px 5px 20px;
  background-color: #1bc42c;
  border-style: none;
  box-shadow: 3px 3px 19px red;
  transition: width height 1s;
  cursor: pointer;
}
.AddButton:active {
  width: 101px;
  height: 35px;
}
.DisabledAddButton {
  width: 100px;
  height: 30px;
  margin: 5px 5px 5px 20px;
  background-color: #1bc42c;
  border-style: none;
}
.DisabledAddButton:hover {
  cursor: no-drop;
}

.DecrementButton {
  width: 20px;
  height: 20px;
  margin: 5px;
  background-color: #8ba2fc;
  border-style: none;
  box-shadow: 2px 2px 19px #1b07f2;
  transition: width height 1s;
  cursor: pointer;
}
.DecrementButton:active {
  width: 21px;
  height: 21px;
}

.DisabledDecrementButton {
  width: 20px;
  height: 20px;
  margin: 5px;
  background-color: #8ba2fc;
  border-style: none;
}
.DisabledDecrementButton:hover {
  cursor: no-drop;
}
.IncrementButton {
  width: 20px;
  height: 20px;
  margin: 5px;
  background-color: #8ba2fc;
  border-style: none;
  box-shadow: 2px 2px 19px #1b07f2;
  cursor: pointer;
}
.IncrementButton:active {
  width: 21px;
  height: 21px;
}
.DisabledIncrementButton {
  width: 20px;
  height: 20px;
  margin: 5px;
  background-color: #8ba2fc;
  border-style: none;
}
.DisabledIncrementButton:hover {
  cursor: no-drop;
}
